@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("/source/fonts/oswaldregular.woff2") format("woff2"), url("/source/fonts/oswaldregular.woff2") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("/source/fonts/oswaldmedium.woff2") format("woff2"), url("/source/fonts/oswaldmedium.woff") format("woff");
}
html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}

button,
input {
  /* 1 */
  overflow: visible;
}

button,
select {
  /* 1 */
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

h1,
h2,
h3,
ul,
ol,
li {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
  font-size: inherit;
  font-style: inherit;
}
a:hover {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  text-decoration: none;
  width: 100%;
  padding: 7px 44px;
  background-color: #68b738;
  display: block;
  box-sizing: border-box;
  text-align: center;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.6);
}

.title {
  font-size: 36px;
  line-height: 36px;
  font-style: italic;
}

.subject {
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
  font-style: italic;
}

.text {
  font-family: "Arial", Helvetica, sans-serif;
  color: #444444;
}

.headline {
  text-transform: uppercase;
  border: none;
  line-height: 20px;
}
.headline--white {
  color: #fff;
}
.headline--grey {
  background-color: #f2f2f2;
  color: #444444;
}

.header {
  max-width: 1440px;
  margin: 0 auto;
  height: 66px;
}
@media (min-width: 768px) {
  .header__wrap {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
}

.header__logo {
  display: flex;
  align-items: center;
}
.header__logo .logo__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo .logo__img:hover {
  opacity: 0.8;
}
.header__logo .logo__img:active {
  opacity: 0.6;
}
.header__logo .logo__img:first-child {
  margin-right: 55px;
}

.nav__toggle {
  margin-left: auto;
  position: relative;
  width: 24px;
  height: 18px;
  background-color: #fff;
  border: none;
  cursor: pointer;
}
.nav__toggle:hover {
  opacity: 0.6;
}
.nav__toggle:active {
  opacity: 0.3;
}
.nav__toggle--opened::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  top: 50%;
  left: 0;
  transform: rotate(45deg);
}
.nav__toggle--opened::after {
  position: absolute;
  content: "";
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  top: 50%;
  left: 0;
  transform: rotate(-45deg);
}
.nav__toggle--opened .nav__list {
  z-index: 2;
}
.nav__toggle--closed::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  top: 50%;
  left: 0;
  box-shadow: 0 8px 0 0 #000, 0 -8px 0 0 #000;
}
.nav__toggle--closed .nav__list {
  display: none;
}
.nav__toggle--nojs {
  display: none;
}
.nav__toggle--nojs .nav__list {
  display: block;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__list {
  position: absolute;
  content: "";
  top: 66px;
  left: 0;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 66px 66px 66px;
  list-style: none;
  background-color: #fff;
}
.nav__list .nav__item {
  display: grid;
  text-align: center;
  height: 66px;
  border-bottom: 1px solid #e6e6e6;
  border-top: 1px solid #e6e6e6;
  align-items: center;
}
.nav__list .nav__link {
  align-items: center;
  color: #000000;
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  position: relative;
}
.nav__list .nav__link:hover {
  opacity: 60%;
}
.nav__list .nav__link:active {
  border-bottom: #68b738 1px solid;
}
@media (min-width: 768px) {
  .nav__list .nav__link--current::after {
    position: absolute;
    content: "";
    height: 2px;
    width: 100%;
    background-color: #68b738;
    bottom: 10px;
    left: 0;
  }
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
    justify-content: space-between;
  }
}

body {
  background: #ffffff;
  font-family: "Oswald";
  font-weight: 400;
  font-size: 14px;
  min-height: 100%;
}

.wrap {
  margin: 0 auto;
  padding: 10px 20px 18px;
  overflow: hidden;
  max-width: 320px;
}
@media (min-width: 768px) {
  .wrap {
    max-width: 768px;
  }
}
@media (min-width: 1440px) {
  .wrap {
    max-width: 1440px;
  }
}